body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.App-header {
  background-color: #282c34;
  color: white;
  padding: 60px 20px;
  text-align: center;
}

section {
  padding: 50px 20px;
  max-width: 900px;
  margin: 0 auto;
}

h2 {
  color: #222;
  margin-bottom: 20px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.project-card {
  display: block;
  background-color: white;
  padding: 20px;
  border-radius: 12px;
  text-decoration: none;
  color: #007bff;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  transition: transform 0.2s;
}

.project-card:hover {
  transform: translateY(-5px);
}

footer.contact {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 40px 20px;
}

footer a {
  color: #61dafb;
  text-decoration: none;
}
